import { useState } from "react";
import { getEventStatistics, getPerformancesByEvent } from "../../queries";
import { Athlete, Performance } from "../../types";
import { useSelector } from "react-redux";
import { AnalysisState } from "../../slice";
import HeadToHeadRow from "../../components/Rows/HeadToHeadRow/HeadToHeadRow";
import { formatPerformance } from "../../formatters";
import ResultComparisonRow from "../../components/Rows/ResultComparisonRow/ResultComparisonRow";
import styles from "./headToHead.module.css";

interface DataComparisonSectionProps {
    athleteA: Athlete;
    athleteB: Athlete;
    comparisonSheet: Record<string, Record<string, Record<string, number>>>;
    headToHeadPerformances: Performance[][];
}

const DataComparisonSection = ({
    athleteA,
    athleteB,
    comparisonSheet,
    headToHeadPerformances,
}: DataComparisonSectionProps) => {
    const allPerformances = useSelector(
        (state: AnalysisState) => state.performances
    );
    const allEvents = useSelector((state: AnalysisState) => state.events);
    const [selectedEventId, setSelectedEventId] = useState("ALL");

    const handleSelectEvent = (event: React.ChangeEvent<HTMLSelectElement>) => {
        setSelectedEventId(event.target.value);
    };

    // check this not same as below
    if (
        !(
            athleteA &&
            athleteB &&
            Object.keys(comparisonSheet).length &&
            Object.keys(comparisonSheet[athleteA.id][athleteB.id]).length > 0
        )
    ) {
        return null;
    }

    // check this
    if (
        Object.keys(comparisonSheet).length &&
        Object.keys(comparisonSheet[athleteA.id][athleteB.id]).length === 0
    ) {
        return <div>Never competed!</div>;
    }

    const eventStatsAthleteA = getEventStatistics(
        {
            ...getPerformancesByEvent(
                allPerformances.filter((p) => p.athleteId === athleteA.id)
            ),
            ALL: allPerformances.filter((p) => p.athleteId === athleteA.id),
        },
        [...allEvents, { id: "ALL", name: "ALL" }]
    );
    const eventStatsAthleteB = getEventStatistics(
        {
            ...getPerformancesByEvent(
                allPerformances.filter((p) => p.athleteId === athleteB.id)
            ),
            ALL: allPerformances.filter((p) => p.athleteId === athleteB.id),
        },
        [...allEvents, { id: "ALL", name: "ALL" }]
    );
    return (
        eventStatsAthleteA &&
        eventStatsAthleteB && (
            <>
                <div className="event-select">
                    <select
                        value={selectedEventId}
                        onChange={handleSelectEvent}
                    >
                        {athleteA &&
                            athleteB &&
                            Object.keys(comparisonSheet).length &&
                            Object.keys(
                                comparisonSheet[athleteA.id][athleteB.id]
                            ).map((eventId) => (
                                <option value={eventId}>
                                    {eventId !== "ALL"
                                        ? allEvents.find(
                                              ({ id }) => id === eventId
                                          )?.name
                                        : "ALL"}
                                </option>
                            ))}
                    </select>
                </div>
                {(comparisonSheet[athleteA.id][athleteB.id][selectedEventId] >
                    0 ||
                    comparisonSheet[athleteB.id][athleteA.id][selectedEventId] >
                        0) && (
                    <HeadToHeadRow
                        name="Head To Head Wins"
                        a={
                            comparisonSheet[athleteA.id][athleteB.id][
                                selectedEventId
                            ]
                        }
                        b={
                            comparisonSheet[athleteB.id][athleteA.id][
                                selectedEventId
                            ]
                        }
                    />
                )}
                {selectedEventId !== "ALL" && (
                    <HeadToHeadRow
                        name="Personal Best"
                        a={eventStatsAthleteA[selectedEventId]?.best}
                        b={eventStatsAthleteB[selectedEventId]?.best}
                        formattedA={formatPerformance(
                            eventStatsAthleteA[selectedEventId]?.best,
                            allEvents.find(({ id }) => id === selectedEventId)
                                ?.name
                        )}
                        formattedB={formatPerformance(
                            eventStatsAthleteB[selectedEventId]?.best,
                            allEvents.find(({ id }) => id === selectedEventId)
                                ?.name
                        )}
                    />
                )}
                {eventStatsAthleteA[selectedEventId]?.seasonBest &&
                    eventStatsAthleteB[selectedEventId]?.seasonBest &&
                    selectedEventId !== "ALL" && (
                        <HeadToHeadRow
                            name="Season Best"
                            a={
                                eventStatsAthleteA[selectedEventId]
                                    ?.seasonBest ?? 0
                            }
                            b={
                                eventStatsAthleteB[selectedEventId]
                                    ?.seasonBest ?? 0
                            }
                            formattedA={formatPerformance(
                                eventStatsAthleteA[selectedEventId]
                                    ?.seasonBest ?? 0,
                                allEvents.find(
                                    ({ id }) => id === selectedEventId
                                )?.name
                            )}
                            formattedB={formatPerformance(
                                eventStatsAthleteB[selectedEventId]
                                    ?.seasonBest ?? 0,
                                allEvents.find(
                                    ({ id }) => id === selectedEventId
                                )?.name
                            )}
                        />
                    )}
                <HeadToHeadRow
                    name="Total Performances"
                    a={eventStatsAthleteA[selectedEventId]?.totalPerformances}
                    b={eventStatsAthleteB[selectedEventId]?.totalPerformances}
                />
                <HeadToHeadRow
                    name="Win Count"
                    a={eventStatsAthleteA[selectedEventId]?.winCount}
                    b={eventStatsAthleteB[selectedEventId]?.winCount}
                />
                <HeadToHeadRow
                    name="Longest Win Streak"
                    a={eventStatsAthleteA[selectedEventId]?.longestWinStreak}
                    b={eventStatsAthleteB[selectedEventId]?.longestWinStreak}
                />
                <HeadToHeadRow
                    name="Win Rate"
                    a={eventStatsAthleteA[selectedEventId]?.winRate}
                    b={eventStatsAthleteB[selectedEventId]?.winRate}
                    formatValueAs="percentage"
                />
                <HeadToHeadRow
                    name="Top 3 Rate"
                    a={eventStatsAthleteA[selectedEventId]?.topThreeRate}
                    b={eventStatsAthleteB[selectedEventId]?.topThreeRate}
                    formatValueAs="percentage"
                />
                <div className={styles.results}>
                    {headToHeadPerformances.length > 0 && <div>Results</div>}
                    {headToHeadPerformances.map((ps) =>
                        ps[0].eventId === selectedEventId ||
                        selectedEventId === "ALL" ? (
                            <ResultComparisonRow
                                performances={ps}
                                event={
                                    selectedEventId === "ALL"
                                        ? allEvents.find(
                                              ({ id }) => ps[0].eventId === id
                                          )?.name
                                        : undefined
                                }
                            />
                        ) : null
                    )}
                </div>
            </>
        )
    );
};

export default DataComparisonSection;
