import { useState } from "react";
import { Link } from "react-router-dom";
import styles from "./header.module.css";

export const MenuIcon = () => (
    <div className={styles.menuIcon}>
        <div className={styles.bar}></div>
        <div className={styles.bar}></div>
        <div className={styles.bar}></div>
    </div>
);

const HeaderMenu = () => {
    const [showMenuContents, setShowMenuContents] = useState(false);

    const toggleShowMenuContents = () => {
        setShowMenuContents(!showMenuContents);
    };

    return (
        <div className={styles.headerMenu} onClick={toggleShowMenuContents}>
            <MenuIcon />
            <div
                className={`${styles.menuContent} ${
                    showMenuContents ? "" : styles.hide
                }`}
            >
                <Link className={styles.menuItem} to="/">
                    Home
                </Link>
                <Link className={styles.menuItem} to="/athlete">
                    Athlete
                </Link>
                <Link className={styles.menuItem} to="/meeting">
                    Meeting
                </Link>
                <Link className={styles.menuItem} to="/headtohead">
                    Head To Head
                </Link>
                <Link className={styles.menuItem} to="/news">
                    News
                </Link>
                <Link className={styles.menuItem} to="/contact">
                    Contact
                </Link>
            </div>
            <div
                className={`${styles.appMenuBlanket} ${
                    showMenuContents ? "" : styles.hidden
                }`}
            ></div>
        </div>
    );
};

export default HeaderMenu;
